.about {
    padding-top: 120px;
    &__wrapper {
        display: grid;
        grid-template-columns: 400px 670px;
        grid-template-rows: minmax(245px auto) minmax(255px auto);
        grid-auto-rows: minmax(245px auto);
        column-gap: 70px;
        row-gap: 40px; }
    &__photo {
        grid-row: 1 / 3;
        height: 100%;
        width: 100%;
        object-fit: cover; }
    &__descr {
        padding-top: 6px;
        .divider {
            margin: 0;
            margin-top: 20px; } }

    &__subtitle {
        margin-top: 20px; }
    &__title {
        position: relative;
        padding: 1px 0 0 12px;
        @include before_circle; }
    &__text {
        margin-top: 20px;
        font-size: 14px;
        line-height: 21px; }
    &__skills {
        display: grid;
        grid-template: repeat(2, minmax(115px, auto)) / repeat(2, 260fr);
        grid-auto-rows: 1fr;
        column-gap: 50px;
        row-gap: 20px;
        &-item {
            display: grid;
            grid-template-columns: 40px 200px;
            column-gap: 20px; }
        &-circle {
            position: relative;
            padding: 5px 0 0 15px;
            @include before_circle; }

        &-text {
            margin-top: 5px;
            font-size: 12px;
            line-height: 18px; } } }

