.portfolio {
    padding-top: 120px;
    &__title {
        text-align: center;
        @include before_circle;
        &::before {
            top: 113px;
            left: 50%;
            transform: translateX(-50%); } }
    &__subtitle {
        text-align: center;
        margin-top: 20px; }
    &__wrapper {
        margin-top: 60px;
        display: grid;
        grid-template: repeat(2, 280px) / repeat(4, 280px);
        grid-auto-rows: 280px;
        gap: 5px;
        .vertical {
            grid-row: 1 / 3;
            grid-column: 4 / 5; }
        .horizonal {
            grid-row: 2 / 3;
            grid-column: 2 / 4; } }

    &__item {
        transition: all 0.6s;
        height: 100%;
        width: 100%;
        object-fit: cover;
        img {
            /** width: 100% **/ }
        &:hover {
            transform: scale(1.05); } } }

.w {
    width: 100%;
    height: 100%;
    object-fit: cover; }

.wei {
    width: 100%;
    height: 100%;
    object-fit: cover; }

